'use client';

import React, { useState, useEffect } from 'react';
import './form.css';

interface District {
    id: number;
    name: string;
}

interface Panchayath {
    panchayathId: number;
    panchayathName: string;
    dstId: number;
    dstName: string;
}

// Explicit form data typing
interface FormDataType {
     id?: number;
    registrationNumber: string;
    district: string;
    panchayath: string;
    ward: string;
    area: string;
    date: string;
    diagnosis: string;
    category: string;
    name: string;
    age: string;
    gender: string;
    spouseName: string;
    fatherName: string;
    motherName: string;
    adhaarAddress: string;
    currentAddress: string;
    phoneNumber1: string;
    phoneNumber2: string;
    adhaarNumber: string;
    contactPerson: string;
    relation: string;
    contactPhone: string;
    neighbourResidence: string;
    neighbourPhone: string;
    communityVolunteer: string;
    communityVolunteerPhone: string;
    wardMember: string;
    wardMemberPhone: string;
    aashaVolunteer: string;
    aashaVolunteerPhone: string;
    otherPerson: string;
    otherPersonPhone: string;
    houseRoute: string;
    photograph: File | null;
    aadharDoc: File | null;
    previewPhotoUrl?: string;
    previewAadharName?: string;
}

export default function HospitalRegistrationForm() {
    const today = new Date().toISOString().split('T')[0];

    const [formData, setFormData] = useState<FormDataType>({
        registrationNumber: '',
        district: '',
        panchayath: '',
        ward: '',
        area: '',
        date: today,
        diagnosis: '',
        category: '',
        name: '',
        age: '',
        gender: '',
        spouseName: '',
        fatherName: '',
        motherName: '',
        adhaarAddress: '',
        currentAddress: '',
        phoneNumber1: '',
        phoneNumber2: '',
        adhaarNumber: '',
        contactPerson: '',
        relation: '',
        contactPhone: '',
        neighbourResidence: '',
        neighbourPhone: '',
        communityVolunteer: '',
        communityVolunteerPhone: '',
        wardMember: '',
        wardMemberPhone: '',
        aashaVolunteer: '',
        aashaVolunteerPhone: '',
        otherPerson: '',
        otherPersonPhone: '',
        houseRoute: '',
        photograph: null,
        aadharDoc: null,
    });

    const [districts, setDistricts] = useState<District[]>([]);
    const [panchayaths, setPanchayaths] = useState<Panchayath[]>([]);
    const [selectedDistrictId, setSelectedDistrictId] = useState<number | null>(null);
    const [selectedPanchayathId, setSelectedPanchayathId] = useState<number | null>(null);
    const [isEditMode, setIsEditMode] = useState(false);
    const [buttonText, setButtonText] = useState("Submit");

    const categories = ['A', 'B', 'C'];

    useEffect(() => {

        const fetchLocations = async () => {
            try {
                const [dRes, pRes] = await Promise.all([
                    fetch('https://localhost:7112/api/Districts'),
                    fetch('https://localhost:7112/api/Panchayaths'),
                ]);
                const dData = await dRes.json();
                const pData = await pRes.json();
                setDistricts(dData);
                setPanchayaths(pData);
            } catch (err) {
                console.error('Failed to fetch districts/panchayaths', err);
            }
        };

        const raw = localStorage.getItem("editFormData");
        const isEdit = localStorage.getItem("isEditMode") === "true";
        setIsEditMode(isEdit);

        const isEditMode = localStorage.getItem("isEditMode") === "true";

        if (isEditMode && raw) {
            const p = JSON.parse(raw) as Record<string, any>;

            const dig = <T,>(...keys: string[]): T | "" =>
                keys.reduce<T | "">((out, k) => (out !== "" ? out : p?.[k] ?? ""), "");

            setFormData({
                registrationNumber: dig("registrationNumber", "regNo", "Regno"),
                district: dig("district"),
                panchayath: dig("panchayath"),
                ward: dig("ward"),
                area: dig("area"),
                date: String(dig("date")).substring(0, 10),
                diagnosis: dig("diagnosis"),
                category: dig("category"),
                name: dig("name"),
                age: dig("age"),
                gender: dig("gender") === 0 || dig("gender") === "0" ? "Male"
                    : dig("gender") === 1 || dig("gender") === "1" ? "Female" : "",
                spouseName: dig("spouseName"),
                fatherName: dig("fatherName"),
                motherName: dig("motherName"),
                adhaarAddress: dig("adhaarAddress"),
                currentAddress: dig("currentAddress"),
                phoneNumber1: dig("phoneNumber1"),
                phoneNumber2: dig("phoneNumber2"),
                adhaarNumber: dig("adhaarNumber"),
                contactPerson: dig("contactPerson"),
                relation: dig("relation"),
                contactPhone: dig("contactPhone"),
                neighbourResidence: dig("neighbourResidence"),
                neighbourPhone: dig("neighbourPhone"),
                communityVolunteer: dig("communityVolunteer"),
                communityVolunteerPhone: dig("communityVolunteerPhone"),
                wardMember: dig("wardMember"),
                wardMemberPhone: dig("wardMemberPhone"),
                aashaVolunteer: dig("aashaVolunteer"),
                aashaVolunteerPhone: dig("aashaVolunteerPhone"),
                otherPerson: dig("otherPerson"),
                otherPersonPhone: dig("otherPersonPhone"),
                houseRoute: dig("houseRoute"),
                photograph: null,
                aadharDoc: null,
            });

            localStorage.removeItem("editFormData");
            localStorage.removeItem("isEditMode");
        }

        fetchLocations();
    }, []);


     const handleChange = (e: React.ChangeEvent<any>) => {
    const { name, value, files } = e.target;
    if (files) {
      if (name === "photograph" && files[0]) {
        setFormData(prev => ({
          ...prev,
          photograph: files[0],
          previewPhotoUrl: URL.createObjectURL(files[0]),
        }));
      }
      if (name === "aadharDoc" && files[0]) {
        setFormData(prev => ({
          ...prev,
          aadharDoc: files[0],
          previewAadharName: files[0].name,
        }));
      }
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }
  };

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();


        const formElement = e.target as HTMLFormElement;

        const form = new FormData(formElement);

        localStorage.removeItem("editFormData");
        localStorage.removeItem("isEditMode");

        // Basic Info
        form.append("registrationNumber", formData.registrationNumber);
        form.append("district", formData.district);
        form.append("panchayath", formData.panchayath);
        form.append("ward", formData.ward);
        form.append("area", formData.area);
        form.append("date", formData.date);
        form.append("diagnosis", formData.diagnosis);
        form.append("category", formData.category);

        // Personal Info
        form.append("name", formData.name);
        form.append("age", formData.age.toString()); // age is number, convert to string
        form.append("gender", formData.gender);
        form.append("spouseName", formData.spouseName);
        form.append("fatherName", formData.fatherName);
        form.append("motherName", formData.motherName);
        form.append("adhaarAddress", formData.adhaarAddress);
        form.append("currentAddress", formData.currentAddress);
        form.append("phoneNumber1", formData.phoneNumber1);
        form.append("phoneNumber2", formData.phoneNumber2);
        form.append("adhaarNumber", formData.adhaarNumber);
        form.append("contactPerson", formData.contactPerson);
        form.append("relation", formData.relation);
        form.append("contactPhone", formData.contactPhone);

        // Reference Info
        form.append("neighbourResidence", formData.neighbourResidence);
        form.append("neighbourPhone", formData.neighbourPhone);
        form.append("communityVolunteer", formData.communityVolunteer);
        form.append("communityVolunteerPhone", formData.communityVolunteerPhone);
        form.append("wardMember", formData.wardMember);
        form.append("wardMemberPhone", formData.wardMemberPhone);
        form.append("aashaVolunteer", formData.aashaVolunteer);
        form.append("aashaVolunteerPhone", formData.aashaVolunteerPhone);
        form.append("otherPerson", formData.otherPerson);
        form.append("otherPersonPhone", formData.otherPersonPhone);

        // Extra
        form.append("houseRoute", formData.houseRoute);

        if (formData.photograph) form.append("photograph", formData.photograph);
        if (formData.aadharDoc) form.append("aadharDoc", formData.aadharDoc);

        if (isEditMode && formData.id) {
      form.append("Id", String(formData.id));
    }

    Object.entries(formData).forEach(([k, v]) => {
      if (typeof v === 'string') form.append(k, v);
    });
    if (formData.photograph) form.append("photograph", formData.photograph);
    if (formData.aadharDoc)    form.append("aadharDoc", formData.aadharDoc);

    const url = isEditMode
      ? `https://localhost:7112/api/Hpforms/${formData.id}`
      : "https://localhost:7112/api/Hpforms";
    const method = isEditMode ? "PUT" : "POST";

    const res = await fetch(url, { method, body: form });
    if (!res.ok) {
      console.error("Submission failed", await res.text());
      alert("Submission failed. Check console.");
      return;
    }
    

        console.log("FORMDATA BEING SENT", formData);

        try {
            const res = await fetch("https://localhost:7112/api/Hpforms", {
                method: "POST",
                body: form,
                // Do NOT set Content-Type header here; browser sets it automatically for FormData
            });

            if (!res.ok) throw new Error(await res.text());

            alert("Submitted successfully!");
        } catch (err) {
            console.error("Submission failed", err);
            alert("Submission failed. Check console for error details.");
        }
        alert(isEditMode ? "Updated successfully!" : "Submitted successfully!");
    window.location.href = "/admform/viewpage"; // redirect to listing
    };

    return (
        <main className="min-h-screen w-full bg-gradient-to-br from-blue-50 to-blue-100 px-10 py-8">
            
            <form id="hospital-form" onSubmit={handleSubmit}>

                <h1 id="main-heading">HOSPITAL Application Form</h1>

                <section>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

                        <h2 className="text-xl font-semibold mb-4 text-center md:text-left">Registration Details</h2>
                        <br />

                        {/* Registration Number */}
                        <div>
                            <label htmlFor="registrationNumber" className="block font-medium mb-1">
                                Registration Number
                            </label>
                            <input
                                id="registrationNumber"
                                name="registrationNumber"
                                type="text"
                                value={formData.registrationNumber}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                placeholder="Enter registration number"
                            />
                        </div>

                        {/* District Dropdown */}
                        <div>
                            <label htmlFor="district" className="block font-medium mb-1">
                                District
                            </label>
                            <select
                                id="district"
                                name="district"
                                value={formData.district}
                                onChange={(e) => {
                                    const selectedName = e.target.value;
                                    const selectedDistrict = districts.find(d => d.name === selectedName);
                                    setSelectedDistrictId(selectedDistrict?.id || null);

                                    setFormData(prev => ({
                                        ...prev,
                                        district: selectedName,
                                        panchayath: '', // Clear Panchayath when District changes
                                    }));
                                }}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                            >
                                <option value="">Select District</option>
                                {districts.map(d => (
                                    <option key={d.id} value={d.name}>
                                        {d.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        {/* Panchayath Dropdown */}
                        <div>
                            <label htmlFor="panchayath" className="block font-medium mb-1">
                                Panchayath
                            </label>
                            <select
                                id="panchayath"
                                name="panchayath"
                                value={formData.panchayath}
                                onChange={(e) => {
                                    const selectedName = e.target.value;
                                    const selectedP = panchayaths.find(p => p.panchayathName === selectedName);

                                    setSelectedPanchayathId(selectedP?.panchayathId || null);

                                    setFormData(prev => ({
                                        ...prev,
                                        panchayath: selectedName,
                                    }));
                                }}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                            >
                                <option value="">Select Panchayath</option>
                                {panchayaths
                                    .filter(p => p.dstId === selectedDistrictId)
                                    .map(p => (
                                        <option key={p.panchayathId} value={p.panchayathName}>
                                            {p.panchayathName}
                                        </option>
                                    ))}
                            </select>
                        </div>


                        <div>
                            <label htmlFor="ward" className="block font-medium mb-1">
                                Ward No.
                            </label>
                            <input
                                id="ward"
                                name="ward"
                                type="tel"
                                inputMode="numeric"
                                value={formData.ward}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                placeholder="Enter Ward No."
                            />
                        </div>

                        <div>
                            <label htmlFor="area" className="block font-medium mb-1">
                                Area / Route
                            </label>
                            <input
                                id="area"
                                name="area"
                                type="text"
                                value={formData.area}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                placeholder="Enter area or route"
                            />
                        </div>

                        <div>
                            <label htmlFor="date" className="block font-medium mb-1">
                                Date
                            </label>
                            <input
                                id="date"
                                name="date"
                                type="date"
                                value={formData.date}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                            />
                        </div>

                        <div className="md:col-span-2">
                            <label htmlFor="diagnosis" className="block font-medium mb-1">
                                Diagnosis
                            </label>
                            <textarea
                                id="diagnosis"
                                name="diagnosis"
                                maxLength={1000}
                                rows={4}
                                value={formData.diagnosis}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                placeholder="Enter diagnosis details (max 1000 characters)"
                            />
                        </div>

                        <div className="md:col-span-2">
                            <label htmlFor="category" className="block font-medium mb-1">
                                Category
                            </label>
                            <select
                                id="category"
                                name="category"
                                value={formData.category}
                                onChange={handleChange}
                                required
                                className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                            >
                                <option value="">Select Category</option>
                                {categories.map(c => (
                                    <option key={c} value={c}>{c}</option>
                                ))}
                            </select>
                        </div>

                    </div>

                </section>


                <section>
                    <h2>Personal Information</h2>
                    <div className="section-grid">
                        <div>
                            <label htmlFor="name">Name</label>
                            <input type="text" id="name" name="name"
                                onChange={handleChange}
                                required />
                        </div>

                        <div>
                            <label htmlFor="age">Age</label>
                            <input type="number"
                                inputMode="numeric"
                                id="age" name="age" min="1" required
                                onChange={handleChange} />
                        </div>

                        {/* Gender */}
                        <div>
                            <label htmlFor="gender">Gender</label>
                            <select id="gender" name="gender" defaultValue="" onChange={handleChange}>
                                <option value="" disabled>-- Select Gender --</option>
                                <option value="male">Male</option>
                                <option value="female">Female</option>
                                <option value="other">Other</option>
                            </select>
                        </div>

                        <div>
                            <label htmlFor="spouseName">Spouse Name</label>
                            <input type="text" id="spouseName" name="spouseName" onChange={handleChange} />
                        </div>

                        <div>
                            <label htmlFor="fatherName">Father Name</label>
                            <input type="text" id="fatherName" name="fatherName" onChange={handleChange} />
                        </div>

                        <div>
                            <label htmlFor="motherName">Mother Name</label>
                            <input type="text" id="motherName" name="motherName" onChange={handleChange} />
                        </div>

                        <div>
                            <label htmlFor="adhaarNumber">Aadhar Number</label>
                            <input
                                type="text"
                                id="adhaarNumber"
                                name="adhaarNumber"
                                onChange={handleChange}
                                maxLength={14}
                                placeholder="0000 - 0000 - 0000"
                                inputMode="numeric"
                                pattern="\d{4} ?-? ?\d{4} ?-? ?\d{4}"
                                title="Enter a valid 12-digit Aadhar number in the format 0000 - 0000 - 0000"
                            />
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 col-span-full">
                            <div>
                                <label htmlFor="adhaarAddress" className="block mb-1 font-medium">Adhaar Address</label>
                                <input
                                    type="text"
                                    id="adhaarAddress"
                                    onChange={handleChange}
                                    name="adhaarAddress"
                                    className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                    placeholder="Enter Adhaar address"
                                />
                            </div>

                            <div>
                                <label htmlFor="currentAddress" className="block mb-1 font-medium">Current Address</label>
                                <input
                                    type="text"
                                    id="currentAddress"
                                    onChange={handleChange}
                                    name="currentAddress"
                                    className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                    placeholder="Enter current address"
                                />
                            </div>
                        </div>


                        <div style={{ gridColumn: '1 / -1' }}>
                            <label htmlFor="houseRoute">House Route (In Detail)</label>
                            <textarea
                                id="houseRoute"
                                name="houseRoute"
                                maxLength={1000}
                                onChange={handleChange}
                                rows={5}
                                placeholder="Describe your house route in detail (up to 1000 characters)"
                            ></textarea>
                        </div>

                        <div>
                            <label htmlFor="phoneNumber1">Phone Number 1</label>
                            <input type="tel"
                                id="phoneNumber1"
                                name="phoneNumber1"
                                pattern="[0-9]{10}"
                                onChange={handleChange}
                                onInput={(e) => {
                                    const input = e.target as HTMLInputElement;

                                    input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                }} required />
                        </div>

                        <div>
                            <label htmlFor="phoneNumber2">Phone Number 2</label>
                            <input type="tel"
                                pattern="[0-9]{10}"
                                onChange={handleChange}
                                onInput={(e) => {
                                    const input = e.target as HTMLInputElement;
                                    input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                }}
                                inputMode="numeric"
                                maxLength={10} id="phoneNumber2" name="phoneNumber2" />
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 col-span-full">
                            {/* Photograph Upload */}
                            <div>
                                <label htmlFor="photograph" className="block font-semibold mb-2 text-blue-700">Upload Photograph</label>
                                <div className="flex items-center gap-4">
                                    <input
                                        type="file"
                                        id="photograph"
                                        name="photograph"

                                        accept="image/*"
                                        className="file:py-2 file:px-4 file:rounded-lg file:border-0 file:text-white file:bg-blue-600 hover:file:bg-blue-700 transition cursor-pointer"
                                        onChange={(e) => {
                                            const input = e.target as HTMLInputElement;
                                            const file = input.files?.[0];
                                            if (file) {
                                                const imgElement = document.getElementById("photoPreview") as HTMLImageElement;
                                                imgElement.src = URL.createObjectURL(file);
                                                setFormData(prev => ({ ...prev, photograph: file }));
                                            }
                                        }}

                                    />
                                    <img id="photoPreview" alt="Preview" className="w-16 h-16 object-cover rounded-md border" />
                                </div>
                            </div>

                            {/* Aadhar PDF Upload */}
                            <div>
                                <label htmlFor="aadharDoc" className="block font-semibold mb-2 text-blue-700">Upload Aadhar Document (PDF)</label>
                                <div className="flex items-center gap-4">
                                    <input
                                        type="file"
                                        id="aadharDoc"
                                        name="aadharDoc"
                                        accept="application/pdf"
                                        className="file:py-2 file:px-4 file:rounded-lg file:border-0 file:text-white file:bg-blue-600 hover:file:bg-blue-700 transition cursor-pointer"
                                        onChange={(e) => {
                                            const input = e.target as HTMLInputElement;
                                            const file = input.files?.[0];
                                            const label = document.getElementById("pdfPreview") as HTMLSpanElement;
                                            label.textContent = file ? file.name : "No file chosen";
                                            if (file) {
                                                setFormData(prev => ({ ...prev, aadharDoc: file }));
                                            }
                                        }}

                                    />
                                    <span id="pdfPreview" className="text-sm text-gray-600 truncate max-w-[160px]">No file chosen</span>
                                </div>
                            </div>
                        </div>


                        <div className="col-span-full mt-6">
                            <label className="block text-lg font-semibold mb-2 text-gray-700"></label>
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                                <div>
                                    <label htmlFor="contactPerson" className="block mb-1 font-medium">Contact Person</label>
                                    <input
                                        type="text"
                                        id="contactPerson"
                                        name="contactPerson"
                                        className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                        placeholder="Name"
                                        onChange={handleChange}
                                    />
                                </div>

                                <div>
                                    <label htmlFor="relation" className="block mb-1 font-medium">Relation</label>
                                    <input
                                        type="text"
                                        id="relation"
                                        name="relation"
                                        onChange={handleChange}
                                        className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                        placeholder="e.g. Brother"
                                    />
                                </div>

                                <div>
                                    <label htmlFor="contactPhone" className="block mb-1 font-medium">Phone No</label>
                                    <input
                                        type="tel"
                                        id="contactPhone"
                                        name="contactPhone"
                                        inputMode="numeric"
                                        onChange={handleChange}
                                        pattern="[0-9]{10}"
                                        maxLength={10}
                                        placeholder="10-digit number"
                                        onInput={(e) => {
                                            const input = e.target as HTMLInputElement;
                                            input.value = input.value.replace(/\D/g, '').slice(0, 10);
                                        }}
                                        className="w-full border rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-600"
                                    />
                                </div>
                            </div>
                        </div>



                    </div>
                </section>

                <section>
                    <h2>Emergency Contact</h2>
                    <div className="flex flex-col gap-4">
                        {/* Neighbour Residence */}
                        <div className="flex gap-4 items-center">
                            <div className="w-1/2">
                                <label htmlFor="neighbourResidence" className="block">Neighbour Residence</label>
                                <input type="text" id="neighbourResidence" name="neighbourResidence" className="w-full p-2 border rounded" onChange={handleChange} />
                            </div>
                            <div className="w-1/2">
                                <label htmlFor="neighbourPhone" className="block">Phone No</label>
                                <input type="tel"
                                    onChange={handleChange}
                                    pattern="[0-9]{10}"
                                    onInput={(e) => {
                                        const input = e.target as HTMLInputElement;
                                        input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10

                                    }}
                                    inputMode="numeric"
                                    maxLength={10} id="neighbourPhone" name="neighbourPhone" className="w-full p-2 border rounded" />
                            </div>
                        </div>

                        {/* Community Volunteer */}
                        <div className="flex gap-4 items-center">
                            <div className="w-1/2">
                                <label htmlFor="communityVolunteer" className="block">Community Volunteer</label>
                                <input type="text" id="communityVolunteer" name="communityVolunteer" className="w-full p-2 border rounded" onChange={handleChange} />
                            </div>
                            <div className="w-1/2">
                                <label htmlFor="communityVolunteerPhone" className="block">Phone No</label>
                                <input type="tel"
                                    pattern="[0-9]{10}"
                                    onChange={handleChange}
                                    onInput={(e) => {
                                        const input = e.target as HTMLInputElement;
                                        input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                    }}
                                    inputMode="numeric"
                                    maxLength={10} id="communityVolunteerPhone" name="communityVolunteerPhone" className="w-full p-2 border rounded" />
                            </div>
                        </div>

                        {/* Ward Member */}
                        <div className="flex gap-4 items-center">
                            <div className="w-1/2">
                                <label htmlFor="wardMember" className="block">Ward Member</label>
                                <input type="text" id="wardMember" name="wardMember" className="w-full p-2 border rounded" onChange={handleChange} />
                            </div>
                            <div className="w-1/2">
                                <label htmlFor="wardMemberPhone" className="block">Phone No</label>
                                <input type="tel"
                                    pattern="[0-9]{10}"
                                    onChange={handleChange}
                                    onInput={(e) => {
                                        const input = e.target as HTMLInputElement;
                                        input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                    }}
                                    inputMode="numeric"
                                    maxLength={10} id="wardMemberPhone" name="wardMemberPhone" className="w-full p-2 border rounded" />
                            </div>
                        </div>

                        {/* Aasha Volunteer */}
                        <div className="flex gap-4 items-center">
                            <div className="w-1/2">
                                <label htmlFor="aashaVolunteer" className="block">Aasha Volunteer</label>
                                <input type="text" id="aashaVolunteer" name="aashaVolunteer" className="w-full p-2 border rounded" onChange={handleChange} />
                            </div>
                            <div className="w-1/2">
                                <label htmlFor="aashaVolunteerPhone" className="block">Phone No</label>
                                <input type="tel"
                                    pattern="[0-9]{10}"
                                    onChange={handleChange}
                                    onInput={(e) => {
                                        const input = e.target as HTMLInputElement;
                                        input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                    }}
                                    inputMode="numeric"
                                    maxLength={10} id="aashaVolunteerPhone" name="aashaVolunteerPhone" className="w-full p-2 border rounded" />
                            </div>
                        </div>

                        {/* Other Person */}
                        <div className="flex gap-4 items-center">
                            <div className="w-1/2">
                                <label htmlFor="otherPerson" className="block">Other Person</label>
                                <input type="text" id="otherPerson" name="otherPerson" className="w-full p-2 border rounded " onChange={handleChange} />
                            </div>
                            <div className="w-1/2">
                                <label htmlFor="otherPersonPhone" className="block">Phone No</label>
                                <input type="tel"
                                    pattern="[0-9]{10}"
                                    onChange={handleChange}
                                    onInput={(e) => {
                                        const input = e.target as HTMLInputElement;
                                        input.value = input.value.replace(/\D/g, '').slice(0, 10); // only digits, max 10
                                    }}
                                    inputMode="numeric"
                                    maxLength={10} id="otherPersonPhone" name="otherPersonPhone" className="w-full p-2 border rounded" />
                            </div>
                        </div>
                    </div>
                </section>



                <button
                    type="submit"
                    className="mt-8 w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 rounded-lg transition"
                >
                   {isEditMode ? "Update Form" : "Submit Form"}
                </button>
            </form>
        </main >
    );
}
